@font-face {
  font-family: RobotoCondensed;
  src: url(assets/roboto-condensed.light.ttf);
  font-weight: 800;
}
@font-face {
  font-family: EconomicaBold;
  src: url(assets/economica-bold.ttf);
}
body {
  text-align: center;
  font-size: 20px;
  padding: 2.5%;
  font-family: "RobotoCondensed";
}
h1,
h2,
h3,
h4,
h5 {
  font-family: "EconomicaBold";
}
.track {
  width: 250px;
  height: 250px;
  display: inline-block;
  position: relative;
  margin: 20px;
  cursor: pointer;
}
.track-image {
  width: 250px;
  height: 250px;
}
.track-name {
}
